<template>
  <div class="source_swipe">
    <vx-header :showback="1" :showreset="1" :title="$t('common.sourceSwitch')" @reset="reset"></vx-header>
    <div class="vx-container">
      <vx-tcnbtn></vx-tcnbtn>
      <div class="source_swipe_content">
        <vx-cell class="vc-cell" :title="$t('common.quickSelect')" :value="selectedWin.name" @link="selectSource"></vx-cell>
        <div class="sources-box">
          <div class="sources">
            <div class="sources-item" :class="{border:selected.id === source.id}" v-for="(source,index) in inputSourceList" :key="index" @click.stop="selSource(source)">
              <div class="detail">
                <div class="icon">
                  <img v-if="source.status" :src="source.activeIcon" alt="">
                  <img v-else :src="source.normalIcon" alt="">
                </div>
                <div class="info" v-if="source.status !== 0">
                  {{source.width}}*{{source.height}}@{{source.refresh}}Hz
                </div>
                <div class="info" v-else>
                  No signal
                </div>
              </div>
              <div class="operator">
                <div class="source_name">{{source.name}}</div>
                <!-- <div class="edit" @click.stop="edit">
                <img src="../assets/common/edit.png" alt="">
              </div> -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <mt-popup v-model="setSource" position="bottom">
      <div class="window-list" :style="{height:45*sources.length / 75 * 2 +'rem'}" v-show="setSource">
        <div class="list-item" v-for="(source,index) in sources" :class="{actived:source.id === selectedWin.id}" @click="selectedWin = source;setSource =false;selected.id = selectedWin.inputSourceId;">
          <span>{{source.name}}</span>
          <img v-show="source.id === selectedWin.id" src="../assets/common/checked.png">
        </div>
      </div>
    </mt-popup>
    <div class="model" v-show="model"></div>
  </div>
</template>

<script>
  import { Toast, Indicator } from 'mint-ui';
  import htp from "../api/http";
  import { getLoc } from 'src/utils';
  import url from '../api/apiurl';
  import { setTimeout } from 'timers';
  export default {
    data() {
      return {
        model: false,//模态层，配和loding使用
        deviceName: '',
        selectedWin: {
        },
        selected: {
          id: 0
        },
        inputSourceList: [],
        setSource: false,
        sources: []
      };
    },
    created() {
      this.deviceName = getLoc('device').deviceName;
      this.initPage();
    },
    methods: {
      initPage() {
        this.getInputSourceList();
        this.getSourceList();
      },
      reset() {
        this.initPage();
      },
      //窗口选择
      selectSource() {
        if(this.sources.length > 0) {
          this.setSource = !this.setSource;
        } else {
          Toast({
            message: this.$t('window.open'),
            duration: 1000
          });
        }
      },
      //获取源切换列表
      getInputSourceList: function() {
        htp.get(url.getInputSourceList).then(res => {
          if(res.status === 10000000) {
            this.inputSourceList = res.data.inputSourceList;
            this.inputSourceHandler();
          }
        });
      },
      getSourceList() {
        htp.get(url.getWinList).then(res => {
          if(res.status === 10000000) {
            this.sources = res.data.winList.filter((v, i) => {
              return v.status === 1;
            });
            if(this.sources.length) {
              this.selectedWin = this.sources[0];
              this.selected.id = this.selectedWin.inputSourceId;
            }
          }
        });
      },
      inputSourceHandler() {
        this.inputSourceList.forEach(source => {
          switch(source.type) {
            case 0:
              source.normalIcon = require('../assets/source_icons/dvi.png');
              source.activeIcon = require('../assets/source_icons/dvi_active.png');
              break;
            case 7:
            case 8:
              source.normalIcon = require('../assets/source_icons/dp.png');
              source.activeIcon = require('../assets/source_icons/dp_active.png');
              break;
            case 1:
            case 6:
            case 9:
              source.normalIcon = require('../assets/source_icons/hdmi.png');
              source.activeIcon = require('../assets/source_icons/hdmi_active.png');
              break;
            case 2:
              source.normalIcon = require('../assets/source_icons/vga.png');
              source.activeIcon = require('../assets/source_icons/vga_active.png');
              break;
            case 4:
              source.normalIcon = require('../assets/source_icons/cvbs.png');
              source.activeIcon = require('../assets/source_icons/cvbs_active.png');
              break;
            case 3:
              source.normalIcon = require('../assets/source_icons/sdi.png');
              source.activeIcon = require('../assets/source_icons/sdi_active.png');
              break;
            case 10:
              source.normalIcon = require('../assets/source_icons/usb.png');
              source.activeIcon = require('../assets/source_icons/usb_active.png');
              break;
            case 11:
              source.normalIcon = require('../assets/source_icons/hd_base.png');
              source.activeIcon = require('../assets/source_icons/hd_base_active.png');
              break;
            default:
              source.normalIcon = require('../assets/source_icons/wz.png');
              source.activeIcon = require('../assets/source_icons/wz_active.png');
              break;
          }
        });
      },
      //点击源事件
      selSource(source) {
        this.selected.id = source.id;
        if(this.selectedWin.id) {
          if(!window.offline) {
            Indicator.open({
              text: getLoc('lang') == 'en' ? 'Loading...' : '加载中...',
              spinnerType: 'triple-bounce'
            });
            this.model = true;
          }
          htp.post(url.setWinInput, {
            selectWindow: this.selectedWin.id,
            selectInput: this.selected.id
          })
            .then(res => {
              Indicator.close();
              this.model = false;
              if(res.status === 10000001) {
                Toast(this.$t('common.requestFailed'));
              }
            });
          setTimeout(() => {
            Indicator.close();
            this.model = false;
          }, 2000);
        } else {
          Toast({
            message: this.$t('window.open'),
            duration: 1000
          });
        }
      }
    }
  };
</script>
<style scoped lang="less">
  .vc-cell {
    position: absolute !important;
    width: 100%;
    top: 30px;
  }
  .model {
    position: fixed;
    width: 100%;
    height: 100%;
    background: #000000;
    z-index: 30;
    opacity: 0.3;
  }
  .vx-container {
    z-index: 0;
    overflow: hidden;
  }
  .border {
    border: 2px solid #ff6700;
    /*no*/
  }
  .sources-box {
    height: 100%;
    overflow-y: auto;
  }

  .source_swipe_content {
    margin-top: 30px;
    height: 100%;
    padding-top: 110px;
    .sources {
      margin: 20px auto;
      width: 85%;
      display: flex;
      flex-wrap: wrap;
      .sources-item {
        background-color: #41434b;
        .detail {
          width: 100%;
          height: 150px;
          .icon {
            height: 90px;
            line-height: 90px;
            text-align: center;
            img {
              display: inline-block;
              vertical-align: middle;
              width: 146px;
              height: 90px;
            }
          }
          // .info {
          //   height: 60px;
          //   line-height: 60px;
          //   font-size: 22px;
          //   color: #909090;
          // }
        }
        .operator {
          border-top: 1px solid #2b2c33;
          /*no*/
          height: 60px;
          width: 100%;
          position: relative;
          .source_name {
            line-height: 60px;
            color: #a4a4a4;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            word-wrap: break-word;
            word-break: break-all;
            font-size: 14px;
            /*no*/
          }
          .edit {
            height: 60px;
            line-height: 60px;
            width: 50px;
            position: absolute;
            right: 0;
            top: 0;
            img {
              width: 30px;
              height: 30px;
              vertical-align: middle;
            }
          }
        }
      }
      .info {
        height: 60px;
        line-height: 60px;
        font-size: 22px;
        color: #909090;
      }
    }
    .operator {
      border-top: 1px solid #2b2c33;
      /*no*/
      height: 60px;
      width: 100%;
      position: relative;
      .source_name {
        line-height: 60px;
        color: #a4a4a4;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        word-wrap: break-word;
        word-break: break-all;
      }
      .edit {
        height: 60px;
        line-height: 60px;
        width: 50px;
        position: absolute;
        right: 0;
        top: 0;
        img {
          width: 30px;
          height: 30px;
          vertical-align: middle;
        }
      }
    }
  }
  .source_swipe {
    .mint-popup {
      height: auto;
    }
  }

  .info {
    .vx6-info_content_top {
      margin-top: -50px;
    }
    .vx6-info_content_top_lable {
      color: #e0e0e1;
      margin-top: 35px;
      display: block;
    }
    .vx6-info-content-bottom {
      color: #7c7c7f;
      position: absolute;
      left: 15px;
      right: 15px;
      bottom: 95px;
    }
    .info-content {
      position: absolute;
      top: 38px;
      width: 100%;
      bottom: 0;
      min-height: 380px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  @media screen and (orientation: portrait) {
    .sources-item {
      width: 46%;
      height: 210px;
      margin-left: 8%;
      margin-bottom: 30px;
      box-sizing: border-box;
      box-shadow: 2px 5px 5px #171719;
      border-radius: 6px;
      &:nth-child(2n + 1) {
        margin-left: 0;
      }
    }
  }

  /*横屏模式*/

  @media screen and (orientation: landscape) {
    .sources-item {
      width: 22%;
      height: 210px;
      margin-left: 4%;
      margin-bottom: 30px;
      box-sizing: border-box;
      box-shadow: 2px 5px 5px #171719;
      border-radius: 6px;
      &:nth-child(4n + 1) {
        margin-left: 0;
      }
    }
  }
</style>

<style lang="less">
  .window-list {
    width: 100%;
    background: #6b707d;
    z-index: 9;
    border-bottom: 1px solid #787e8d; /*no*/
    .list-item {
      height: 90px;
      line-height: 90px;
      border-top: 1px solid #787e8d; /*no*/
      color: #fff;
      font-size: 28px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 40px;
      img {
        width: 40px;
        height: auto;
      }
    }
  }
</style>



